@import 'normalize.scss';
@import 'reset.scss';

* {
    box-sizing: border-box; }

html {
    overflow-x: hidden;
    overflow-y: scroll; }
body {
    font-family: 'Poppins', sans-serif;
    display: block;
    background-color: #121113;
    // overflow: hidden
    // overflow-x: hidden  //* отключает горизонтальный скролл */
 }    // overflow-y: scroll

a {
    text-decoration: none;
    color: #000; }


.section__nav {}
.section__nav_container {}

.section__nav_info-container {
    display: flex;
    float: left;
    margin-right: 5px;
    position: relative;
    top: 245px;
    padding-left: 30px; }
.section__nav_info {
    border: 1px solid #FFFFFF;
    border-radius: 25px;
    // width: 200px
    margin-right: 5px;
    width: 330px;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    &:nth-child(3) {
        margin-right: 0px; }
    a {
        color: #FFFFFF;
        font-size: 24px;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center; } }



.section__nav_photo {
    width: 450px;
    height: 450px;
    overflow: hidden;
    margin: 0;
    top: 25px;
    position: relative;
    // left: 430px
    &-img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 30px; } }


.section__nav_about-container {
    background-color: #2E2D2E;
    border-radius: 30px;
    width: 1000px;
    height: 460px;
    position: relative;
    overflow: hidden;
    left: 30px;
    right: 30px;
    bottom: 120px;
    margin: 0; }
.section__nav_about {
    position: relative;
    width: 100%;
    height: 100%; }

.section__nav_about-title-container {
    background-color: #FFCA43;
    border-radius: 25px;
    width: 180px;
    height: 40px;
    margin-left: 50px;
    margin-top: 50px;
    // margin-bottom: 14px
    justify-content: center;
    align-items: center;
    // gap: 10px
    display: flex; }
.section__nav_about-title {
    color: #111111;
    font-size: 16px;
    font-weight: 700; }

.section__nav_about-name {
    color: #FFCA43;
    font-size: 48px;
    font-weight: 500;
    margin-top: 140px;
    margin-left: 50px; }
.section__nav_about-text {
    color: #FFCA43;
    font-size: 16px;
    font-weight: 400;
    line-height: 30px;
    margin-top: 60px;
    margin-left: 50px;
    width: 600px;
    height: 90px; }

