.section__nav_about-btn-plus {
    position: relative;
    left: 885px;
    bottom: 70px;
    display: inline-block;
    width: 70px;
    height: 70px;
    background: linear-gradient(#FFCA43,#FFCA43), linear-gradient(#FFCA43,#FFCA43);
    background-position: center;
    background-size: 50% 1px,1px 50%; //*thickness = 2px, length = 50% (25px)*/
    background-repeat: no-repeat;
    border: 1px #FFCA43 solid;
    border-radius: 9999px;
    cursor: pointer; }

.section__nav_two-blocks {
    position: relative;
    left: 1035px;
    bottom: 430px;
    display: flex; }

.section__nav_portfolio-container {
    background-color: #7841FF;
    border-radius: 25px;
    width: 250px;
    height: 310px;
 }    // cursor: pointer
.section__nav_portfolio {
    width: 100%;
    height: 100%;
    font-size: 48px;
    font-weight: 400;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    h2 {
        color: #FFFFFF; } }
.section__nav_potfolio-arrow {
    width: 130px;
    color: #FFFFFF; }

.section__nav_cv-container {
    background-color: #FF607C;
    border-radius: 25px;
    width: 200px;
    height: 310px;
    margin-left: 5px;
 }    // cursor: pointer
.section__nav_cv {
    width: 100%;
    height: 100%;
    color: #FFFFFF;
    font-size: 48px;
    font-weight: 400;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center; }
.section__nav_cv-arrow {
    width: 110px;
    color: #FFFFFF; }
